html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

img {
  max-width: 100%;
  display: block;
}

p {
  font-size: 1.6rem;
  margin: 0;
  padding: 0;
  line-height: 1.8;
}

a {
  text-decoration: none;
  color: #000000;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1,
h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.img {
  width: 100%;
}

.section {
  padding: 8rem 0;
  overflow: hidden;
}

[class$=heading] {
  margin: 2rem 0;
}

.header {
  padding: 5rem 0;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  font-size: 2.4rem;
  transition: all 0.2s ease;
}
.nav__link:hover {
  color: #f46669;
}

.classes {
  padding: 5rem 0;
  background-color: #f5f5f5;
}
.classes__heading {
  text-align: center;
}
.classes__heading::after {
  content: "";
  display: block;
  height: 10rem;
  margin: 0 auto;
  background-image: url("../img/util/separador.png");
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .classes__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .classes__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.class {
  margin-bottom: 2rem;
  border: 0.1rem solid gainsboro;
  background-color: #ffffff;
}
.class:last-of-type {
  margin-bottom: 0;
}
.class__img {
  width: 100%;
}
.class__metadata {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f46669;
  color: #ffffff;
}
.class__price {
  padding: 0.5rem;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 1rem;
  color: #000000;
  background-color: #c7c57d;
}
.class__content {
  padding: 2rem;
}
.class__description {
  color: #f46669;
}
.class__link {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  transition: all 0.2s ease;
  border: none;
  text-align: center;
  cursor: pointer;
  color: #ffffff;
  background-color: #f46669;
  display: block;
  font-size: 1.6rem;
  padding: 1rem;
}
.class__link:hover {
  background-color: #f1363a;
}
.class__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .class__link {
    width: auto;
  }
}
@media (min-width: 768px) {
  .class {
    margin-bottom: 0;
  }
}

@media (min-width: 768px) {
  .blocks__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
    grid-auto-flow: dense;
  }
}

.block {
  height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.block:nth-child(2), .block:nth-child(4) {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.block:nth-child(1) {
  background-color: #f46669;
}
.block:nth-child(2) {
  background-image: url(../img/block/bloque_1.jpg);
}
.block:nth-child(3) {
  background-color: #c7c57d;
}
@media (min-width: 768px) {
  .block:nth-child(3) {
    grid-column: 2/3;
  }
}
.block:nth-child(4) {
  background-image: url(../img/block/bloque_2.jpg);
}
.block__content {
  max-width: 40rem;
  padding: 2rem 0;
  color: #ffffff;
  border-block: 0.1rem solid #ffffff;
}
.block__heading {
  text-transform: uppercase;
  margin-top: 0;
}
.block__text {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .block {
    height: 30rem;
  }
}

.aboutus {
  padding: 5rem 0;
}
.aboutus__heading {
  text-align: center;
}
.aboutus__heading::after {
  content: "";
  display: block;
  height: 10rem;
  margin: 0 auto;
  background-image: url("../img/util/separador.png");
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .aboutus__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.aboutus__block {
  text-align: center;
}
.aboutus__img {
  margin: 0 auto;
}

.school {
  background-image: url(../img/page/chef_bg.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 14rem 0;
  text-align: center;
  color: #ffffff;
}
.school__heading {
  font-weight: 700;
}
.school__text {
  font-size: 2.6rem;
  max-width: 50rem;
  margin: 0 auto;
}
.school__link {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  transition: all 0.2s ease;
  border: none;
  text-align: center;
  cursor: pointer;
  color: #ffffff;
  background-color: #f46669;
}
.school__link:hover {
  background-color: #f1363a;
}
.school__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .school__link {
    width: auto;
  }
}

.footer {
  padding: 3rem 0;
  border-bottom: 0.5rem solid #f46669;
}
@media (min-width: 768px) {
  .footer__grid {
    display: flex;
    justify-content: space-between;
  }
}
.footer__copyright {
  text-align: center;
  font-size: 2.4rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .footer__copyright {
    margin-top: 0;
  }
}

.mask {
  overflow: hidden;
  width: 100%;
}

.truncate {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}